import back from '@/components/back'
import noData from '@/components/noData'
export default {
	components: {
		back,
		noData
	},
	data() { //引入PopupPicker组件 引入提现路由
		return {
			startColor: 'start1',
			DrId: this.$route.params.id, //医生id
			InYear: 2019, //年
			InMonth: 0, //月
			ServiceType: -1, //类型
			page: 1,
			size: 10,
			show: false, //pitchOn
			expandCategory: [], //类型渲染
			expandActive: {
				CategoryItemName: "全部",
				CategoryItemValue: -1
			}, //选中的类型
			timeShow: false,
			expandTimeValue: '2019-00',
			format: 'YYYY-MM',
			expandTimeTxt: '按月选择',
			expandCategoryTxtTitle: '本月',
			arrList: null,
			haveMore: true,
			temp:false,
			loading: false,
		}
	},
	mounted() {
		//this.httpStart(this.$route.params.start);
		this.getMore()
	},
	created() {
		//获取当前的年月日
		let _date = new Date();//当前日期
		this.InYear = _date.getFullYear()
		this.InMonth = _date.getMonth() + 1
		if(this.InMonth < 10){
			this.InMonth = "0" + this.InMonth
		}
		this.expandTimeValue = this.InYear + "-" + this.InMonth
		this.serviceList(false);
		this.getType()
	},
	methods: {
		getType() {
			//获取type
			this.$get('PlatFormAPI/Category/QueryItemByCategoryId', {
				CategoryId: 58
			}).then(o => {
				this.expandCategory = o
			})
		},
		pitchOnFn(i) {
			this.expandActive = i
		},
		expandCalendarClickFn() {
			if(this.expandTimeTxt == '按年选择') {
				this.format = 'YYYY-MM'
				this.expandTimeTxt = '按月选择'
			} else {
				this.format = 'YYYY'
				this.expandTimeTxt = '按年选择'
			}
		},
		rlClick() {
			this.timeShow = true
		},
		pitchOnBtn() {
			this.show = false
			this.ServiceType = this.expandActive.CategoryItemValue
			this.page = 1
			this.serviceList(false)
		},
		expandCategoryFn() {
			this.show = true
		},
		exchangeConfirm() {
			this.timeShow = false
			if(this.expandTimeValue.length > 4) {
				this.InYear = this.expandTimeValue.slice(0, 4)
				this.InMonth = this.expandTimeValue.slice(5)
				this.expandCategoryTxtTitle = this.expandTimeValue.replace('-', '年') + '月'
			} else {
				this.InYear = this.expandTimeValue
				this.InMonth = 0
				this.expandCategoryTxtTitle = this.expandTimeValue + '年'
			}
			this.page = 1
			this.serviceList(false)
		},
		exchangeEsc() {
			this.timeShow = false
		},
		serviceList(h) {
			let obj = {
				DrId: this.DrId, //医生id
				InYear: this.InYear,
				InMonth: this.InMonth,
				ServiceType: this.ServiceType,
				PageIndex: this.page,
				PageSize: 10
			}
			this.$post('/StatisticAPI/IncomeActive/QueryDoctorInDetailByPage', obj).then(o => {
				if(!o.ReturnData || o.ReturnData.length < 10) {
					this.haveMore = false
				} else {
					this.haveMore = true
				}
				if(h) {
					o.ReturnData.forEach(v => {
						this.arrList.push(v)
					})
				} else {
					this.arrList = o.ReturnData || []
				}
				this.temp = true
				this.loading = false
			})
		},
		accomplishFn(n) {
			switch(n) {
				case 0:
					return '无';
				case 1:
					return '进行中';
				case 2:
					return '已完成';
				case 3:
					return '已退款';
			}
		},
		getMore() { //下拉加载分页
			const el = this.$refs.el
			let timer = null
			window.onscroll = () => {
				clearTimeout(timer)
				timer = setTimeout(() => {
					const top = document.documentElement.scrollTop ||
						window.pageYOffset ||
						document.body.scrollTop,
						h = el.scrollHeight,
						wh = window.innerHeight
					if(h - top - wh < 100 && this.haveMore) {
						this.page += 1
						this.loading = true
						this.serviceList(true)
						
					}
				}, 100)
			}
		}
	},
	beforeDestroy() {
		window.onscroll = null
	}

}



